import {
  LeftModalHeader,
  ScreenWrapper,
  UIFlashList,
  UserAvatar,
} from '@true/ui';
import React, {memo, useCallback, useMemo} from 'react';
import {
  Fonts,
  IC_SEARCH,
  stripSpecialCharacters,
  styled,
  translate,
  useMe,
  useUserGroup,
  useUserGroupByQuery,
} from '@true/core';
import {Button} from '@policy/components/Button';
import {View} from 'react-native';
import {useStorages, useStoragesByQuery} from '@policy/store/storages';
import {
  navigateToOneTabPolicyScreen,
  navigateToSearchScreen,
} from '@policy/utils';
import {
  requestPolicyBySaved,
  requestPolicyBySender,
  requestPolicyByStorage,
} from '@policy/store/policies/functions';
import {Icons} from '@true/asset';

interface IItemData {
  id: string | number;
  type: 'sender' | 'saved' | 'storage';
}

interface ISectionData {
  title: string;
  id: number;
  icon: any;
}

export const MenuScreen = memo(() => {
  const me = useMe();

  const renderItem = useCallback(
    ({item, index}: {item: IItemData; index?: number}) => (
      <Item data={item} index={index} />
    ),
    [],
  );

  const renderSection = useCallback(
    ({
      item,
      index,
      toggle,
    }: {
      item: ISectionData;
      toggle: () => void;
      index: number;
    }) => <Section data={item} index={index} />,
    [],
  );

  const storages = useStoragesByQuery('all');
  const senders = useUserGroupByQuery('teams');

  const data: ISectionData[] = useMemo(
    () => [
      {
        id: 'section_0',
        title: translate('policy.storages'),
        // data: storages.map(id => ({id, type: 'storage'})),
        icon: Icons.Outlined.DOCUMENT_TEXT,
      },
      {
        id: 'section_1',
        title: translate('policy.senders'),
        // data: senders.map(id => ({id, type: 'sender'})),
        icon: Icons.Outlined.BUILDING_OFFICE_2,
      },
      {
        id: 'section_2',
        title: translate('policy.saved'),
        // data: ['expired', 'rejected', 'archived', 'trash'].map(id => ({
        //   id,
        //   type: 'saved',
        // })),
        icon: Icons.Outlined.SAVE,
      },
    ],
    [storages, senders],
  );

  const map: Record<string, IItemData[]> = useMemo(
    () => ({
      section_0: storages.map(id => ({id, type: 'storage'})),
      section_1: senders.map(id => ({id, type: 'sender'})),
      section_2: ['expired', 'rejected', 'archived', 'trash'].map(id => ({
        id,
        type: 'saved',
      })),
    }),
    [storages, senders],
  );

  return (
    <ScreenWrapper isGrey={true}>
      <LeftModalHeader
        hideSeparator={true}
        title={translate('policy.menu')}
        rightComponent={
          <SRow>
            <Button source={IC_SEARCH} onPress={navigateToSearchScreen} />
            {/*<Button source={IC_SETTING} style={styles.headerIcon} />*/}
          </SRow>
        }
      />
      <SUserInfoWrapper>
        <UserAvatar userKey={me?.id || ''} size={40} />
        <SUserView>
          <SUserText>{me?.name}</SUserText>
          <SUserMailText>{me?.email}</SUserMailText>
        </SUserView>
      </SUserInfoWrapper>
      <UIFlashList
        data={data}
        mapKey={'id'}
        map={map}
        estimatedItemSize={50}
        renderItem={renderItem}
        renderSection={renderSection}
      />
    </ScreenWrapper>
  );
});

const SRow = styled.View`
  flex-direction: row;
  margin-right: 16px;
`;

const SUserInfoWrapper = styled.View`
  flex-direction: row;
  padding: 8px 16px;
  align-items: center;
  background-color: ${p => p.theme.backgroundColor};
  border-bottom-width: 1px;
  border-color: ${p => p.theme.neutral5};
`;

const SUserView = styled.View`
  margin-left: 12px;
`;

const SUserText = styled.Text`
  font-size: 16px;
  line-height: 24px;
  font-family: ${Fonts.Regular};

  color: ${p => p.theme.neutral1};
`;

const SUserMailText = styled.Text`
  font-size: 12px;
  line-height: 16px;
  font-family: ${Fonts.Regular};
  color: ${p => p.theme.neutral2};
`;

const Section = memo(
  ({data, index}: {data: {title: string; icon: any}; index?: number}) => {
    return (
      <SSectionContainer>
        <SSectionIcon source={data.icon} />
        <SSectionText>{data.title}</SSectionText>
      </SSectionContainer>
    );
  },
);

const Item = memo(({data, index}: {data: IItemData; index?: number}) => {
  const render = useCallback((text: string, onPress?: () => void) => {
    return (
      <View>
        <SItemContainer onPress={onPress}>
          <SItemText>{text}</SItemText>
        </SItemContainer>
      </View>
    );
  }, []);

  if (data.type === 'sender') {
    const sender = useUserGroup(data.id);

    return render(stripSpecialCharacters(sender?.name), () => {
      navigateToOneTabPolicyScreen({
        params: {id: data.id},
        query: 'sender',
        requestFn: requestPolicyBySender,
        title: sender?.name || '',
        items: ['collection', 'storage', 'receiver', 'doctype'],
      });
    });
  }

  if (data.type === 'storage') {
    const storage = useStorages(data.id);

    return render(stripSpecialCharacters(storage?.name), () => {
      navigateToOneTabPolicyScreen({
        params: {id: data.id},
        query: 'storage',
        requestFn: requestPolicyByStorage,
        title: storage?.name || '',
        items: ['collection', 'doctype'],
      });
    });
  }

  const title = useMemo(
    () =>
      data.id.toString() === 'trash'
        ? translate('policy.soft_deleted')
        : translate('policy.' + data.id.toString()),
    [data.id],
  );

  return render(title, () => {
    navigateToOneTabPolicyScreen({
      params: {tail: data.id},
      query: 'saved',
      requestFn: requestPolicyBySaved,
      title: title,
      items: ['collection', 'storage', 'receiver', 'doctype'],
      mainKey: 'tail',
    });
  });
});

const SSectionContainer = styled.View`
  flex-direction: row;
  align-items: center;
  padding: 12px 16px;
  background-color: ${p => p.theme.backgroundColor};
  margin-top: 8px;
`;
const SSectionText = styled.Text`
  color: ${p => p.theme.neutral1};
  font-size: 16px;
  line-height: 24px;
  font-family: ${Fonts.Medium};
`;
const SSectionIcon = styled.Image`
  margin-right: 8px;
  height: 24px;
  width: 24px;
`;

const SItemContainer = styled.TouchableOpacity`
  padding: 16px;
  background-color: ${p => p.theme.backgroundColor};
`;
const SItemText = styled.Text`
  color: ${p => p.theme.neutral1};
`;
