import { DynamicHeader, ScreenWrapper } from "@true/ui";
import React, { memo } from "react";
import { translate, useNavigationParams } from "@true/core";
import { DEFAULT_PARAMS, IParams } from "@policy/screens/PersonalScreen/index";
import { FilterComp } from "@policy/components/FilterComp";

export interface PersonalFilterProps {
  params: IParams;
  setParams: any;
}

export const PersonalFilter = memo(() => {
  const {params, setParams} = useNavigationParams<PersonalFilterProps>();

  return (
    <ScreenWrapper>
      <DynamicHeader title={translate('policy.filter')} />
      <FilterComp
        items={[
          'collection',
          'folder',
          'storage',
          'receiver',
          'doctype',
          [
            'created_date',
            'effective_date',
            'expired_date',
          ]

        ]}
        params={params}
        setParams={setParams}
        defaultParams={DEFAULT_PARAMS}
      />
    </ScreenWrapper>
  );
});
