import {ScreenWrapper} from '@policy/components';
import React, {memo, useState} from 'react';
import {DynamicHeader} from '@true/ui';
import {Fonts, stripSpecialCharacters, styled, useNavigationParams} from '@true/core';
import {PolicyList} from '@policy/screens/MenuScreen/PolicyList';
import {navigateToDynamicFilterScreen} from '@policy/utils';
import {IInputFilterType} from '@policy/components/FilterComp';
import {FilterIcon} from '@policy/components/FilterIcon';
import {SelectedItemProvider} from '@policy/context/SelectedItemContext';
import {SelectedHeader} from '@policy/screens/HomeScreen/SelectedHeader';
import {useAnimatedSafeAreaInsets} from 'react-native-safe-area-context';

export interface OneTabPolicyScreenProps {
  title: string;
  params: any;
  query: string;
  requestFn: (params: any) => Promise<any>;
  items: IInputFilterType[];
  mainKey?: string;
}

export const OneTabPolicyScreen = memo(() => {
  const {
    title,
    params,
    query,
    requestFn,
    items,
    mainKey = 'id',
  } = useNavigationParams<OneTabPolicyScreenProps>();

  const [_params, setParams] = useState(params);

  const {aTop} = useAnimatedSafeAreaInsets();

  return (
    <ScreenWrapper isGrey={true}>
      <SelectedItemProvider>
        <SViewContent>
          <DynamicHeader
            title={stripSpecialCharacters(title)}
            hideSeparator={true}
            rightComponent={
              <SRow>
                <FilterIcon
                  onPress={() => {
                    navigateToDynamicFilterScreen({
                      params: _params,
                      setParams,
                      items,
                      mainKey,
                    });
                  }}
                  params={{..._params, [mainKey]: undefined}}
                />
              </SRow>
            }
          />
          <SelectedHeader
            title={stripSpecialCharacters(title)}
            animatedTop={aTop}
          />
          <PolicyList
            // ListHeaderComponent={
            //   <STitleWrapper>
            //     <STitle>{stripSpecialCharacters(title)}</STitle>
            //   </STitleWrapper>
            // }
            params={_params}
            query={query}
            requestFn={requestFn}
          />
        </SViewContent>
      </SelectedItemProvider>
    </ScreenWrapper>
  );
});

const STitleWrapper = styled.View`
  padding: 8px 16px 16px;
  background-color: ${p => p.theme.backgroundColor};
  border-bottom-width: 1px;
  border-color: ${p => p.theme.grey4};
`;

const SViewContent = styled.View`
  background-color: ${p => p.theme.neutral9};
  flex: 1;
`;

const STitle = styled.Text`
  font-size: 20px;
  line-height: 28px;
  font-family: ${Fonts.Bold};
  color: ${p => p.theme.neutral1};
`;

const SRow = styled.View`
  flex-direction: row;
  margin-right: 16px;
`;
