import { styled, translate, useNavigationParams } from "@true/core";
import { DynamicHeader, ScreenWrapper } from "@true/ui";
import React, { memo } from "react";
import { DEFAULT_PARAMS, IParams } from "@policy/screens/DocumentTypesScreen/DoctypeDetailScreen";
import { FilterComp } from "@policy/components/FilterComp";

export interface DoctypeFilterProps {
  params: IParams;
  setParams: any;
}

export const DoctypeFilter = memo(() => {
  const {params, setParams} = useNavigationParams<DoctypeFilterProps>();

  return (
    <ScreenWrapper>
      <DynamicHeader title={translate('policy.filter')} />
      <FilterComp
        items={['collection', 'storage']}
        params={params}
        setParams={setParams}
        defaultParams={DEFAULT_PARAMS}
      />
    </ScreenWrapper>
  );
});
